import React, { useState, useEffect } from 'react';

import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";



import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';

import {
  Box,
  Card,
  Image,
  Heading,
  Text,
  Flex,
  Button,
  Link,
} from 'rebass'


import {
  Label,
  Input,
  Select,
  Textarea,
  Radio,
  Checkbox,
} from '@rebass/forms'




var alertText = "É recomendado que você mantenha-se em isolamento social até o dia ";
var isolamento = "";


export default function Body()
{
 


  const [startDate, setStartDate] = useState(new Date());

  useEffect(() =>
  {
    document.getElementById("pergunta1").style.display = 'block';
    document.getElementById("pergunta2").style.display = 'none';
    document.getElementById("pergunta2datepicker").style.display = 'none';
    document.getElementById("alert").style.display = 'none';
    document.getElementById("alert2").style.display = 'none';
    document.getElementById("alert3").style.display = 'none';
  }); 



  function sintomaNao()
  {
    document.getElementById("pergunta2").style.display = 'block';
    document.getElementById("pergunta3").style.display = 'none';
  }

  function sintomaSim()
  {
    document.getElementById("alert").style.display = 'none';
    document.getElementById("alert2").style.display = 'none';
    document.getElementById("alert3").style.display = 'none';
    document.getElementById("pergunta2").style.display = 'none';
    document.getElementById("pergunta2datepicker").style.display = 'none';

    document.getElementById("alert").style.display = 'block';

    var tempData = new Date();
    tempData.setDate(tempData.getDate() + 10);

    document.getElementById("alert").innerHTML = "<strong>Atenção</strong> - É recomendado que você mantenha-se em isolamento social até o dia <strong >" + convertData(tempData) + "</strong>";
  }

  function contatoSim()
  {
    document.getElementById("alert").style.display = 'none';
    document.getElementById("alert2").style.display = 'none';
    document.getElementById("alert3").style.display = 'none';

    document.getElementById("pergunta2datepicker").style.display = 'block';
  }

  function contatoNao()
  {
    document.getElementById("alert").style.display = 'none';
    document.getElementById("alert2").style.display = 'none';
    document.getElementById("alert3").style.display = 'none';

    document.getElementById("pergunta2datepicker").style.display = 'none';
    document.getElementById("alert3").style.display = 'block';
    document.getElementById("alert3").innerHTML = "<strong>Legal</strong> - Aparentemente você não está infectado pelo coronavírus!";
  }

  function contatoData(date)
  {
    document.getElementById("alert").style.display = 'none';
    document.getElementById("alert2").style.display = 'none';
    document.getElementById("alert3").style.display = 'none';

    //document.getElementById("alert").style.display = 'block';
    
    if(date > Date.now())
    {
      //document.getElementById("alert").setAttribute(severity,"error");

      document.getElementById("alert2").style.display = 'block';
      document.getElementById("alert2").innerHTML = "<strong>Erro</strong> - Selecione uma data válida!";
    }
    else
    {
      document.getElementById("alert").style.display = 'block';

      var tempData = date;
      tempData.setDate(tempData.getDate() + 5);

      document.getElementById("alert").innerHTML = "<strong>Atenção</strong> - É recomendado que você mantenha-se em isolamento social até o dia <strong >" + convertData(tempData) + "</strong>";
    }
 }

  function convertData(str)
  {
    var data = new Date(str),
      dia  = data.getDate().toString().padStart(2, '0'),
        mes  = (data.getMonth()+1).toString().padStart(2, '0'),
          ano  = data.getFullYear();
      
    return [dia, mes, ano].join("-");
  }



  return (
    <>
      <Box
        padding="10px"
        width={[ 4/5, 3/4, 1/2 ]}
        color='white'
        bg='transparent'
        >
          <Text
          fontSize={[ 3, 4, 5 ]}
          fontWeight='bold'
          color='white'
          >
          Bem Vindo ao COVPOOL
        </Text>
        <Text
          marginTop="5px"
          fontSize={[ 1, 2, 3 ]}
          color='white'
          >
          Sua plataforma de gestão individual a cerca do contágio COVID-19!
        </Text>

        

        <Box
          id='pergunta1'
          >

          <Text
            marginTop="35px"
            fontSize={[ "12px", "13px", "15px" ]}
            color='white'
          >
            Você está tendo algum sintoma ou está com algum problema de saúde?
          </Text>
        
          <Flex  flexWrap='wrap'>
            <Label width={[ 1/2, 1/4 ]} p={2}>
              <Radio
                id='sintomasim'
                name='sintoma'
                value='sim'
                onClick={() => { sintomaSim()}}
              />
              Sim
            </Label>
            <Label width={[ 1/2, 1/4 ]} p={2}>
              <Radio
                id='sintomanao'
                name='sintoma'
                value='nao'
                onClick={() => { sintomaNao()}}
              />
              Não
            </Label>
            
          </Flex>
        </Box>

        <Box
          id='pergunta2'
          >

          <Text
            marginTop="15px"
            fontSize={[ "12px", "13px", "15px" ]}
            color='white'
          >
            Você teve contato com alguém que estava positivo para COVID19 recentemente?
          </Text>
        
          <Flex  flexWrap='wrap'>
            <Label width={[ 1/2, 1/4 ]} p={2}>
              <Radio
                id='contatosim'
                name='contato'
                value='sim'
                onClick={() => { contatoSim()}}
              />
              Sim
            </Label>
            <Label width={[ 1/2, 1/4 ]} p={2}>
              <Radio
                id='contatonao'
                name='contato'
                value='nao'
                onClick={() => { contatoNao()}}
              />
              Não
            </Label>
            <Box 
              id='pergunta2datepicker'
              marginLeft="20px"
            >
              <Text
                marginTop="5px"
                marginBottom="5px"
                fontSize={[ "12px", "13px", "15px" ]}
                color='white'
              >
                Quando você entrou em contato com essa pessoa?
              </Text>
              <DatePicker
                selected={startDate}
                onChange={date => contatoData(date)}
                dateFormat="dd/MM/yyyy"
                placeholderText="Selecione uma data"
                />

            </Box>
          </Flex>
        </Box>


        

        <Box marginTop="15px">
          <Alert id='alert' variant="filled" severity="warning"></Alert>
          <Alert id='alert2' variant="filled" severity="error"></Alert>
          <Alert id='alert3' variant="filled" severity="success"></Alert>
        </Box>
        
        
      </Box>
      
    </>
  );
}
